@import "vars";

.surveyButton {
    background-color: $button-color;
    border: $border-width solid $button-color;
    display: inline-block;
    color: $button-text-color;
    -webkit-border-radius: $border-radius;
    -moz-border-radius: $border-radius;
    -ms-border-radius: $border-radius;
    border-radius: $border-radius;
    &:hover {
        border-color: darken( $button-color, 10%);
        background-color: darken( $button-color, 10%);
    }
    &.checked {
        border-color: darken($button-color, 20%);
        background-color: darken( $button-color, 10%);
        color: darken( $button-text-color, 20%);
    }
}
@import "vars";

.survey-progress {
  position: fixed;
  top: 0;
  left: 0;
  width: 100% !important;
  background-color: $button-color;
  z-index: 2147483647;
  .survey-progress-bar {
    height: 0.5em;
    background-color: $button-text-color;
    span {
      display: none;
    }
    -webkit-transition: width 1s;
    transition: width 1s;
  }
}
$question-font-size: 1.3em;
$general-spacing: 0.5em;

$button-color: #f4f8fd;
$button-text-color: #478FE2;
$border-width: 0.15em;
$border-radius: 0.25em;

$break-small: 400px;
$break-large: 1200px;
@import "vars";
@import "buttons";
@import "progress";
body {
    #surveyElement {
        font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
        max-width: 35em;
        font-size: $question-font-size;
        margin-bottom: $general-spacing;
        .qtitle {
            font-size: 1em;
        }
        .form-inline {
            padding-left: 20%;
            padding-right: 20%;
            @media screen and (max-width: $break-small) {
              padding-left: 10%;
              padding-right: 10%;
            }
        }
        .question {
            margin-top: 1em;
            text-align: center;
            .radio input[type=radio],
            .checkbox input[type=checkbox] {
                display: none;
            }
            .radio,
            .checkbox {
                label {
                    @extend .surveyButton;
                    width: 100%;
                    font-size: 0.8em;
                    margin-bottom: $general-spacing;
                    padding: $general-spacing;
                }
            }
        }
        .panel-footer {
            background-color: transparent;
            text-align: center;
        }
    }
}